// Typografy -------
html {
  font-size: $font-size; }
body {
  font-family: $font-family;
  &.lock {
    overflow: hidden; } }
// Typografy end ---

.swiper {
  overflow: hidden; }

// main position with header-page-footer
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
.page {
  flex: 1 1 auto;
 }  //padding: 75px 0 0 0
//end-


// Spoiler
.spoiler {
  display: inline-flex;
  align-items: center;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #FFCA41; }

  label {
    cursor: pointer;
    font-family: Bloggersans, serif;
    font-size: rem(18px);
    font-weight: 700;
    color: #FFCA41;
    margin: 0 8px 0 0; }
  &__rest {
    display: none; } }

//

// MODAL WINDOWS
.modal-info {
  font-family: Roboto, serif;
  font-weight: 400;
  line-height: em(24,15);
  max-height: 90vh;
  overflow: scroll;

  background: white;
  padding: 10px 20px;
  max-width: 770px;
  margin: 40px auto;
  position: relative;
  @include breakpoint(sm) {
    padding: 20px 30px; }
  @include breakpoint(md) {
    padding: 100px; }
  &__body {
    p {
      margin: 0 0 em(15,15) 0; } }
  &__title {
    text-transform: uppercase;
    color: #3983C8;
    margin: em(20,15) 0 em(15,15) 0; }
  &__list {
    li {
      padding: 0 0 0 10px;
      position: relative;
      &:before {
        content: '-';
        margin: 0 5px 0 0; } } }
  &__phone {
    display: block;
    color: #65C338; } }
